'use client';

import { useEffect, useState } from 'react';

interface Article {
  title: string;
  link: string;
  description: string;
  pubDate: string;
  category: string[];
}

export default function Articles() {
  const [articles, setArticles] = useState<Article[]>([]);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchArticles = async () => {
      try {
        const res = await fetch('/api/medium-rss');
        if (!res.ok) {
          throw new Error('Erreur lors de la récupération des articles.');
        }
        const data = await res.json();
        setArticles(data);
      } catch (err: any) {
        setError(err.message);
      }
    };

    fetchArticles();
  }, []);

  if (error) {
    return <div>Erreur : {error}</div>;
  }

  /* 
  TODO: à corriger
  */

  return (
    <div>
      <h2>Articles</h2>
      <ul className="antialiased max-w-2xl mb-80 flex flex-row overflow-x-auto mx-4 mt-8 p-10 lg:mx-auto text-center">
        {articles.map((article, index) => (
            <li key={index} /*className="p-2"*/ className="flex-shrink-0 w-80 h-80">
              <a href={article.link} target="_blank" rel="noopener noreferrer">
                <h3>{article.title}</h3>
              </a>
              {/* TODO: ajouter images, améliorer style */}
              {article.category.map((cat, element) => (
                <ul key={element}>
                  <p>{cat}</p>
                </ul>
              ))}
            </li>
        ))}
      </ul>
    </div>
  );
}
